import './styles.css';

// 전역 변수
let storeData = [];
let regionData = [];
let currentStore = null;
let map;
let markers = [];
let infoWindows = [];
let redMarkerIcon;
let yellowMarkerIcon;
// 새로운 전역 변수
let currentSearchMode = 'store'; // 기본값은 상호 검색

// DOM 요소
const searchButton = document.getElementById('search-button');
const searchInput = document.getElementById('store-search');
const videoButtonsWrapper = document.querySelector('.video-buttons-wrapper');
const videoContainer = document.getElementById('video');
const buttons = document.querySelectorAll(".buttons-container button");
const tabs = document.querySelectorAll(".tab-button");
const tabContent = document.getElementById("tab-content");
// DOM 요소 추가
const regionSearchModeButton = document.getElementById('region-search-mode');
const storeSearchModeButton = document.getElementById('store-search-mode');
const currentModeDisplay = document.getElementById('current-mode');

// 검색 모드 변경 함수
function changeSearchMode(mode) {
    currentSearchMode = mode;
    currentModeDisplay.textContent = `현재 검색 모드: ${mode === 'region' ? '지역 검색' : '상호 검색'}`;
    regionSearchModeButton.classList.toggle('active', mode === 'region');
    storeSearchModeButton.classList.toggle('active', mode === 'store');
}

// 검색 모드 버튼 이벤트 리스너
regionSearchModeButton.addEventListener('click', () => changeSearchMode('region'));
storeSearchModeButton.addEventListener('click', () => changeSearchMode('store'));

// 지역 검색 함수
function searchByRegion(query) {
    if (!regionData) {
        console.log('지역 데이터가 로드되지 않았습니다.');
        alert('지역 데이터를 로드하는 데 문제가 발생했습니다.');
        return;
    }

    // 시도와 시군구를 분리하여 검색
    const matchedRegions = regionData.filter(region => 
        (region.시도 && region.시도.toLowerCase().includes(query.toLowerCase())) || 
        (region.시군구 && region.시군구.toLowerCase().includes(query.toLowerCase()))
    );

    // 시군구가 없는 경우를 우선적으로 처리
    const matchedRegion = matchedRegions.find(region => region.시군구 === null) || matchedRegions[0];

    if (matchedRegion) {
        console.log(matchedRegion); // 선택된 지역 정보 출력
        const position = new naver.maps.LatLng(matchedRegion.위도, matchedRegion.경도);
        map.setCenter(position);
        map.setZoom(matchedRegion.시군구 ? 11 : 9);

        const regionStores = storeData.filter(store => 
            store.address.includes(matchedRegion.시도) || 
            (matchedRegion.시군구 && store.address.includes(matchedRegion.시군구)) ||
            store.address.includes(query)
        );

        console.log(regionStores); // 필터링된 가게 정보 출력
        updateMarkers(regionStores);
        if (regionStores.length > 0) {
            updateStoreInfo(regionStores[0]);
        } else {
            alert('해당 지역에 등록된 가게가 없습니다.');
        }
    } else {
        alert('해당 지역을 찾을 수 없습니다.');
    }
}







// 유튜브 비디오 및 버튼 업데이트 함수
function updateVideoAndButtons(store) {
    const videoFrame = document.getElementById('youtubeVideo');

    buttons.forEach(button => {
        const buttonId = button.id;
        if (store.youtubeVideos && store.youtubeVideos[buttonId]) {
            button.onclick = () => {
                const videoId = extractVideoId(store.youtubeVideos[buttonId]);
                if (videoId) {
                    videoFrame.src = `https://www.youtube.com/embed/${videoId}`;
                }
            };
        } else {
            console.error(`해당 가게에 대한 유튜브 영상 ID(${buttonId})가 없습니다.`);
        }
    });
}

// 탭 활성화 함수
function activateTab(tabId, content) {
    tabs.forEach(tab => {
        tab.classList.remove('active');
        if (tab.id === tabId) {
            tab.classList.add('active');
        }
    });
    tabContent.innerHTML = content || '내용이 없습니다.';
}

// 탭 클릭 이벤트 설정
function setupTabListeners() {
    tabs.forEach(tab => {
        tab.addEventListener('click', () => {
            const tabId = tab.id;
            if (tabId === 'detail-tab' && currentStore) {
                const detailContent = `
                    가게명: ${currentStore.name}<br>
                    주소: ${currentStore.address}<br>
                    평점: ${currentStore.rating}<br>
                    합산리뷰: ${currentStore.reviews}<br> 
                    방송: ${currentStore.broadcast}<br>
                    전화번호: ${currentStore.phone}<br>
                    메뉴: ${currentStore.menu.join(", ")}<br>
                    가격: ${currentStore.price}
                `;
                activateTab('detail-tab', detailContent);
            } else {
                const content = tab.dataset.content || '내용이 없습니다.';
                activateTab(tabId, content);
            }
        });
    });
}

// 인스타그램 임베드 업데이트 함수
function updateInstagramEmbed(instaEmbedCode) {
    const instagramPostContainer = document.getElementById('instagram-post');
    
    if (tabContent && instagramPostContainer) {
        tabContent.innerHTML = '';
        instagramPostContainer.innerHTML = '';
        tabContent.innerHTML = instaEmbedCode;

        if (window.instgrm) {
            setTimeout(() => {
                window.instgrm.Embeds.process();
            }, 500);
        } else {
            const script = document.createElement('script');
            script.async = true;
            script.defer = true;
            script.src = "https://www.instagram.com/embed.js";
            document.body.appendChild(script);
        }
    } else {
        console.error("tab-content 또는 instagram-post 요소를 찾을 수 없습니다.");
    }
}

// 인스타그램 탭 핸들러
function setupInstagramTabHandlers() {
    document.getElementById('popular1-tab').addEventListener('click', () => {
        if (currentStore && currentStore.popularInsta1) {
            updateInstagramEmbed(currentStore.popularInsta1);
        }
    });
    document.getElementById('popular2-tab').addEventListener('click', () => {
        if (currentStore && currentStore.popularInsta2) {
            updateInstagramEmbed(currentStore.popularInsta2);
        }
    });
    document.getElementById('latest1-tab').addEventListener('click', () => {
        if (currentStore && currentStore.latestInsta1) {
            updateInstagramEmbed(currentStore.latestInsta1);
        }
    });
    document.getElementById('latest2-tab').addEventListener('click', () => {
        if (currentStore && currentStore.latestInsta2) {
            updateInstagramEmbed(currentStore.latestInsta2);
        }
    });
}

// 가게 정보 업데이트 함수
function updateStoreInfo(store) {
    currentStore = store;
    if (!store) return;

    const detailContent = `
        가게명: ${store.name}<br>
        주소: ${store.address}<br>
        평점: ${store.rating}<br>
        합산리뷰: ${store.reviews}<br>
        방송: ${store.broadcast}<br>
        전화번호: ${store.phone}<br>
        메뉴: ${store.menu.join(", ")}<br>
        가격: ${store.price}
    `;
    activateTab('detail-tab', detailContent);

    document.getElementById('popular1-tab').dataset.content = store.popularInsta1 || '해당 게시물을 가져올 수 없습니다.';
    document.getElementById('popular2-tab').dataset.content = store.popularInsta2 || '해당 게시물을 가져올 수 없습니다.';
    document.getElementById('latest1-tab').dataset.content = store.latestInsta1 || '해당 게시물을 가져올 수 없습니다.';
    document.getElementById('latest2-tab').dataset.content = store.latestInsta2 || '해당 게시물을 가져올 수 없습니다.';

    updateVideoAndButtons(store);
   // 새로운 코드: 유튜브 미리보기 업데이트
    updateYoutubePreview(store);
}

// 새로운 함수: 유튜브 미리보기 업데이트
function updateYoutubePreview(store) {
    const videoFrame = document.getElementById('youtubeVideo');
    if (videoFrame && store.youtubeVideos && store.youtubeVideos.childhood) {
        const videoId = extractVideoId(store.youtubeVideos.childhood);
        if (videoId) {
            videoFrame.src = `https://www.youtube.com/embed/${videoId}`;
        }
    }
}

// 유틸리티 함수: YouTube URL에서 비디오 ID 추출
function extractVideoId(url) {
    const regExp = /^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|\&v=)([^#\&\?]*).*/;
    const match = url.match(regExp);
    return (match && match[2].length === 11) ? match[2] : null;
}

// 상호 검색 함수 (기존의 searchStoresAndRegions 함수를 수정)
function searchByStoreName(query) {
    const matchedStores = storeData.filter(store => 
        store.name.toLowerCase().includes(query.toLowerCase())
    );

    if (matchedStores.length > 0) {
        const store = matchedStores[0];
        const position = new naver.maps.LatLng(store.latitude, store.longitude);
        map.setCenter(position);
        map.setZoom(15);

        updateMarkers([store], true);
        updateStoreInfo(store);
    } else {
        alert('검색 결과가 없습니다.');
    }
}


// 마커 업데이트 함수
function updateMarkers(stores, openInfoWindow = false) {
    // 기존 마커 제거
    markers.forEach(marker => marker.setMap(null));
    markers = [];
    infoWindows = [];

    // 새 마커 생성
    stores.forEach((store, index) => {
        const position = new naver.maps.LatLng(store.latitude, store.longitude);
        const marker = new naver.maps.Marker({
            map: map,
            position: position,
            icon: index === 0 ? yellowMarkerIcon : redMarkerIcon
        });

        markers.push(marker);

        const contentString = `
            <div class="iw_inner" style="font-size:13px; font-weight: 500;">
                <h3 style="font-size:14px; font-weight: 600;">
                    ${store.name}
                </h3>
                <p>
                    주소: ${store.address}<br />
                    전화번호: ${store.phone}<br />
                </p>
            </div>
        `;

        const infowindow = new naver.maps.InfoWindow({
            content: contentString,
            minWidth: 200,
            backgroundColor: "#eee",
            borderColor: "#b40057",
            borderWidth: 5,
        });

        infoWindows.push(infowindow);

        naver.maps.Event.addListener(marker, "click", function () {
            markers.forEach(m => m.setIcon(redMarkerIcon));
            marker.setIcon(yellowMarkerIcon);
            infoWindows.forEach(iw => iw.close());
            infowindow.open(map, marker);
            updateStoreInfo(store);
        });

        if (index === 0 && openInfoWindow) {
            infowindow.open(map, marker);
        }
    });
}

// 지도 초기화 함수
function initializeMap() {
    const mapElement = document.getElementById('map');

    if (!searchButton || !mapElement) {
        console.error('Required DOM elements not found');
        return;
    }

    if (window.naver && naver.maps) {
        const cityhall = new naver.maps.LatLng(37.5666805, 126.9784147);
        map = new naver.maps.Map('map', {
            center: cityhall,
            zoom: 6.5
        });

        redMarkerIcon = {
            url: 'https://maps.google.com/mapfiles/ms/icons/red-dot.png',
            size: new naver.maps.Size(32, 32),
            origin: new naver.maps.Point(0, 0),
            anchor: new naver.maps.Point(16, 32)
        };

        yellowMarkerIcon = {
            url: 'https://maps.google.com/mapfiles/ms/icons/yellow-dot.png',
            size: new naver.maps.Size(32, 32),
            origin: new naver.maps.Point(0, 0),
            anchor: new naver.maps.Point(16, 32)
        };

        loadStoreData();
        loadRegionData();
        setupEventListeners();
        
        // 초기 탭 활성화
        activateTab('detail-tab', '가게를 선택하면 상세 정보가 표시됩니다.');
       changeSearchMode('store'); // 초기 검색 모드를 '상호 검색'으로 설정
        console.log('맛집 지도 애플리케이션이 초기화되었습니다.');

   // 지도 빈 곳 클릭 시 정보창 닫기
        naver.maps.Event.addListener(map, 'click', function() {
            infoWindows.forEach(iw => iw.close());
        });

     // 탭 클릭 이벤트 리스너 추가
        document.querySelectorAll('.tab-button').forEach(tab => {
            tab.addEventListener('click', function() {
                // 지도의 크기를 조정
                naver.maps.Event.trigger(map, 'resize');

                // 지도의 중심을 조정하여 빈 공간을 없앰
                const mapCenter = map.getCenter();
                const mapSize = map.getSize();
                const panX = 0;
                const panY = -(mapSize.height * 0.25); // 지도의 중심을 위로 이동

                map.setCenter(mapCenter);
                setTimeout(() => {
                    map.panBy(panX, panY);
                }, 300);
            });
        });
    }
}


// 가게 데이터 로드 함수
function loadStoreData() {
    fetch('https://tera04162.github.io/dduck/dduck.json')
        .then(response => response.json())
        .then(data => {
            storeData = data;
            createInitialMarkers();
            setupSearchListeners();
            setupTabListeners();
            setupInstagramTabHandlers();
        })
        .catch(error => console.log('Error loading store data:', error));
}

// 지역 데이터 로드 함수
function loadRegionData() {
    fetch('https://tera04162.github.io/dduck/region.json')
        .then(response => {
            if (!response.ok) {
                throw new Error('Network response was not ok ' + response.statusText);
            }
            return response.json();
        })
        .then(data => {
            regionData = data;
            console.log('지역 데이터 로드 완료:', regionData);
        })
        .catch(error => console.log('Error loading region data:', error));
}




// 초기 마커 생성 함수
function createInitialMarkers() {
    storeData.forEach((store, index) => {
        const position = new naver.maps.LatLng(store.latitude, store.longitude);
        const marker = new naver.maps.Marker({
            map: map,
            position: position,
            icon: redMarkerIcon
        });

        markers.push(marker);

        const contentString = `
            <div class="iw_inner" style="font-size:13px; font-weight: 500;">
                <h3 style="font-size:14px; font-weight: 600;">
                    ${store.name}
                </h3>
                <p>
                    주소: ${store.address}<br />
                    전화번호: ${store.phone}<br />
                </p>
            </div>
        `;

        const infowindow = new naver.maps.InfoWindow({
            content: contentString,
            minWidth: 200,
            backgroundColor: "#eee",
            borderColor: "#b40057",
            borderWidth: 5,
        });

        infoWindows.push(infowindow);

        naver.maps.Event.addListener(marker, "click", function () {
            markers.forEach(m => m.setIcon(redMarkerIcon));
            marker.setIcon(yellowMarkerIcon);

            setTimeout(() => {
                naver.maps.Event.trigger(map, 'resize');
                const markerPosition = marker.getPosition();
                const mapSize = map.getSize();

                const panX = 0;
                const panY = -(mapSize.height * 0.25);

                map.setCenter(markerPosition);
                setTimeout(() => {
                    map.panBy(panX, panY);
                }, 300);
            }, 300);

            infoWindows.forEach(iw => iw.close());
            infowindow.open(map, marker);

            updateStoreInfo(store);
        });

        console.log(`Marker for ${store.name} created at (${store.latitude}, ${store.longitude})`);
    });

    console.log(`Total markers created: ${markers.length}`);
}

// 검색 리스너 설정 함수
function setupSearchListeners() {
    searchButton.addEventListener('click', performSearch);
    searchInput.addEventListener('keypress', function(e) {
        if (e.key === 'Enter') {
            performSearch();
        }
    });
}

function performSearch() {
    const searchQuery = searchInput.value.trim();
    if (searchQuery.length >= 2) {
        if (currentSearchMode === 'region') {
            searchByRegion(searchQuery);
        } else {
            searchByStoreName(searchQuery);
        }
    } else {
        alert('검색어를 2글자 이상 입력해주세요.');
    }
}

// 이벤트 리스너 설정 함수
function setupEventListeners() {
    setupSearchListeners();
    setupTabListeners();
    setupInstagramTabHandlers();

    window.addEventListener('resize', function() {
        if (map) {
            naver.maps.Event.trigger(map, 'resize');
        }
    });
}
